html {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

body {
  --main-color: #00acdc;
  margin: 0;
}

header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  justify-content: space-around;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  font-size: 12px;
  line-height: 60px;
  list-style-type: none;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: #000;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
